.about-parent-container-div {
    width: 100%;
    background-color: rgb(0, 0, 0);
    margin: 0 0 75px 0;
}

.about-h3 {
    width: 100%;
    text-align: center;
    font-weight: 200;
    color: white;
}

.about-container {
    width: 100%;
    height: 500px;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.aboutMe-picture-div {
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

.aboutMe-container-div {
    width: 40%;
    height: 100%;
    border: 1px dotted gray;
    border-radius: 10px;
    background-color: black;
    overflow: auto;
}

.aboutMe-picture-div > div {
    width: 220px;
    height: 220px;
    background-image: url("../../assets/images/grad_pic.jpg");
    background-size: cover;
    border-radius: 100%;
}

.aboutMe-container-div > p {
    padding: 20px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    color: #f0f0f0;
    font-size: large;
}

@media(width <= 650px) {
    .about-container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .aboutMe-picture-div {
        width: 100%;
        height: 30%;
    }

    .aboutMe-container-div {
        width: 100%;
        height: 70%;
    }

    .about-h3 {
        font-size: 15px;
    }

    .aboutMe-picture-div > div {
        width: 150px;
        height: 150px;
    }

    .aboutMe-container-div > p {
        width: 90%;
        height: 100%;
        font-size: 15px;
    }

}


@media(width <= 475px) {
    .about-h3 {
        font-size: 13px;
    }

    .aboutMe-container-div > p {
        font-size: 12px;
    }
}

@media(width <= 275px) {
    .about-h3 {
        font-size: 11px;
    }

    .aboutMe-container-div > p {
        font-size: 9px;
    }
}