.contact-container {
    display: flex;
    flex-direction: row;
    margin: 20px 0;
}

.contact-comp {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

label {
    color: white;
}

input {
    background-color: rgb(100, 100, 100, 0.3);
    padding: 10px;
    color: white;
    width: 100%;
    box-sizing: border-box;
    border-radius: 3px;
}

input:focus {
    color: white;
}

.form-contact {
    width: 80%;
    
}

.names {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
}

.name {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.contact-me {
    text-align: center;
    color: white;
    font-weight: 200;
}

.email-container, .text-container, .button-container, .subject-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 10px;
}

.email {
    width: 90%;
}


.text-area {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

textarea {
    width: 100%;
    height: 55vh;
    background-color: rgb(100, 100, 100, 0.3);
    color: white;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 10px;
}

.bttn {
    color: white;
    background-color: black;
    border: 1px solid white;
    padding: 5px;
}

.bttn:hover {
    background-color: white;
    border-color: black;
    cursor: pointer;
    color: black;
}


.subject-div {
    width: 90%;
    display: flex;
    flex-direction: column;
}

.subject {
    box-sizing: border-box;
}


@media(width <= 550px) {
    .contact-me {
        font-size: medium;
    }

    .name {
        width: 40%;
    }
    
    label {
        font-size: small;
    }

    input {
        padding: 5px;
    }
    
}

@media(height >= 650px) {
    .text-area {
        height: 45vh;
    }   
}

@media(width <= 300px) {
    textarea, input, label {
        font-size: 12px;
    }

    .contact-me {
        font-size: 15px;
    }
}